import Button from ".";

type Props = {
    children: JSX.Element | string;
    click: () => void;
    color?: string;
    border?: boolean;
};

export default function IconButtonSm({
    border = true,
    color,
    children,
    click,
}: Props) {
    return (
        <Button
            className={`sm-icon-btn ${color} ${!border && "borderless"}`}
            onClick={click}
        >
            {children}
        </Button>
    );
}
