import BlueLogo from "../../assets/blue_logo.png";
import FacebookIcon from "../SVG/FacebookIcon";
import InstagramIcon from "../SVG/InstagramIcon";
import TikTokIcon from "../SVG/TikTokIcon";
import TwitterIcon from "../SVG/TwitterIcon";
import YoutubeIcon from "../SVG/YoutubeIcon";

export default function Footer({ isMobile }: { isMobile: boolean }) {
    return (
        <>
            {!isMobile ? (
                <div className="footer-container">
                    <div>
                        <img src={BlueLogo} />
                    </div>
                    <div>
                        <div>
                            <div>Twitter</div>
                            <div>Instagram</div>
                            <div>Facebook</div>
                            <div>WaveXR.com</div>
                        </div>
                    </div>
                </div>
            ) : (
                <div className="mobile-footer">
                    <div className="mobile-footer-wrapper">
                        <div>2021 Wave</div>
                        <div><TikTokIcon fill={"#FFFFFF"}/></div>
                        <div><YoutubeIcon fill={"#FFFFFF"}/></div>
                        <div><FacebookIcon fill={"#FFFFFF"}/></div>
                        <div><InstagramIcon fill={"#FFFFFF"}/></div>
                        <div><TwitterIcon fill={"#FFFFFF"}/></div>
                        <div>wavexr.com</div>
                    </div>
                </div>
            )}
        </>
    );
}
