import React, { useState } from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";

export const Context = React.createContext({ isMobile: true, resize: () => {} });
const Provider = ({children}: any) => {
  const [isMobile, setMobile] = useState(window.innerWidth < 768)

  const resize = () => {
    if (window.innerWidth < 768) {
      setMobile(true)
    } else {
      setMobile(false)
    }
  }

  return (
    <Context.Provider value={{resize, isMobile}}>{children}</Context.Provider>
  )

}
ReactDOM.render(
    <Provider>
        <React.StrictMode>
            <App />
        </React.StrictMode>
    </Provider>,
    document.getElementById("root")
);
