import { useContext } from "react";
import { Context } from "..";
import ButtonContainer from "../components/button/ButtonContainer";
import IconButtonSm from "../components/button/IconButtonSm";
import SignupButton from "../components/button/SignupButton";
import Arrow from "../components/SVG/Arrow";
import FacebookIcon from "../components/SVG/FacebookIcon";
import InstagramIcon from "../components/SVG/InstagramIcon";
import TwitchIcon from "../components/SVG/TwitchIcon";
import TwitterIcon from "../components/SVG/TwitterIcon";
import YoutubeIcon from "../components/SVG/YoutubeIcon";
// import Arrow from "../components/SVG/Arrow";

export default function Hero() {
    const { isMobile } = useContext(Context);

    const simulateSignup = () => {
        const maestroSignupButton = document.querySelector(
            ".signupbutotnwhatever"
        ) as HTMLButtonElement;
        if (maestroSignupButton) {
            maestroSignupButton.click();
        }
    };

    return (
        <div className="full-view hero">
            <div className="hero-wrapper">
                <div className="performer-name">Performer Name</div>
                <div className="hero-time">00.00 0PM PST</div>
                <div>
                    <div className={`signup-wrapper ${isMobile ? "mobile" : ""}`}>
                        <div className="arrow"> <Arrow /> </div>
                        <SignupButton click={simulateSignup} />
                    </div>
                    <div className="hero-bottom">
                        {!isMobile && (
                            <>
                                <div style={{ fontSize: 30 }}>and you can</div>
                                <div className="oversized-text">
                                    GROW SHROOMS
                                </div>
                            </>
                        )}
                        <div className="small-cta">
                            <div>or watch the limited experience</div>
                            <ButtonContainer styles={{width:100}}>
                                <IconButtonSm click={() => alert("YT")}>
                                    <YoutubeIcon fill={"#FFFFFF"} />
                                </IconButtonSm>
                                <IconButtonSm click={() => alert("TW")}>
                                    <TwitchIcon fill={"#FFFFFF"} />
                                </IconButtonSm>
                            </ButtonContainer>
                        </div>
                        {isMobile && (<><div className="small-cta">
                            <div>tell your friends</div>
                            <ButtonContainer>
                                <IconButtonSm border={false} click={() => alert("FB")}>
                                    <FacebookIcon fill={"#FFFFFF"} />
                                </IconButtonSm>
                                <IconButtonSm border={false} click={() => alert("IG")}>
                                    <InstagramIcon fill={"#FFFFFF"} />
                                </IconButtonSm>
                                <IconButtonSm border={false} click={() => alert("TW")}>
                                    <TwitterIcon fill={"#FFFFFF"} />
                                </IconButtonSm>
                            </ButtonContainer>
                        </div>
                        <div className="small-cta">
                            read the faq
                        </div>
                        <div className="small-cta">
                            learn about wave
                        </div></>)}
                    </div>
                </div>
            </div>
            {!isMobile && (<div className="hero-footer">
                <div>learn about wave</div>
                <div>read the faq</div>
                <div className="social-wrapper">
                    <div>tell your friends</div>
                    <div>F</div>
                    <div>I</div>
                    <div>T</div>
                </div>
            </div>)}
        </div>
    );
}
