import Pink from "../components/word/Pink";

export default function FAQView() {
    return (
        <div className="faq">
            <div className="big-heading" style={{ marginBottom: 20 }}>
                TOTAL FIASCO FAQ
            </div>
            <div className="faq-line">
                <div>when is it?</div>
                <div>
                    <Pink>THURSDAY 4/1/2021</Pink> <br /> Doors at 6PM PT, show
                    at 6:30
                </div>
            </div>
            <div className="faq-line">
                <div>how much does it cost?</div>
                <div>
                    <Pink>FREE!</Pink> if you'd like to support the show and artist, buy a
                    party favor or costume!
                </div>
            </div>
            <div className="faq-line">
                <div>where can I watch it?</div>
                <div>
                    <a href="https://wave.watch">WAVE.WATCH</a> offers the full
                    interactive experience. <a href="">SIGN UP TO RSVP</a>!
                    <br />
                    The non-interactive experience is simulcast on youtube and
                    twitch
                </div>
            </div>
            <div className="faq-line">
                <div>is it in vr?</div>
                <div>Sorry OG Wavers - this show doesn't support VR</div>
            </div>
            <div className="faq-line">
                <div>is it pre-recorded?</div>
                <div>
                    <Pink>NOPE, IT'S LIVE</Pink>. <br /> Using motion-capture technology,
                    Dillon will perform LIVE from the Wave studio into his own
                    virtual partyverse that YOU help control.
                    <br /> No recording of the show will be posted afterwards,
                    so the only way you can see it is to BE THERE
                </div>
            </div>
            <div className="faq-line">
                <div>your question not here?</div>
                <div>
                    <a href="">CHECK OUT OUR FULL FAQ</a> for more details and
                    further information, or @ us via <a href="">E-mail</a>,{" "}
                    <a href="">Twitter</a>, <a href="">Facebook</a>, or{" "}
                    <a href="">Instagram</a> with specific questions.
                </div>
            </div>
        </div>
    );
}
