import WhiteW from "../../assets/white_logo_solo.png";
import WhiteLogo from "../../assets/white_logo.png";
import YoutubeIcon from "../SVG/YoutubeIcon";
import TwitchIcon from "../SVG/TwitchIcon";
import { BLUE } from "../../styles/colors";
import ButtonContainer from "../button/ButtonContainer";
import IconButtonSm from "../button/IconButtonSm";
export default function FooterTop({ isMobile }: { isMobile: boolean }) {
    return (
        <>
            {!isMobile ? (
                <div className="col-2" style={{padding:"30px 0px"}}>
                    <div className="card-theme col-wrapper">
                        <div className="col-content">
                            <div>sign up to rsvp</div>
                            <div>
                                <img src={WhiteW} />
                            </div>
                        </div>
                    </div>
                    <div className="centered-p">
                        <div>or watch on youtube or twitch</div>
                        <div style={{ width: "50%", margin:"20px auto" }}>
                            <ButtonContainer>
                                <button className="icon-btn" onClick={() => alert("YT")}>
                                    <YoutubeIcon fill={BLUE} />
                                </button>
                                <button className="icon-btn" onClick={() => alert("YT")}>
                                    <TwitchIcon fill={BLUE} />
                                </button>
                            </ButtonContainer>
                        </div>
                    </div>
                </div>
            ) : (
                <div>
                    <div className="card-theme">
                        <div className="card-wrapper">
                            <div className="centered-p">
                                <a
                                    style={{
                                        color: "white",
                                        textDecoration: "none",
                                    }}
                                    href="https://wave.watch"
                                >
                                    be in the show at wave.watch/fiasco
                                </a>
                            </div>
                            <div>
                                <img
                                    style={{
                                        width: "50%",
                                        margin: "auto",
                                        display: "block",
                                        padding: 40,
                                    }}
                                    src={WhiteLogo}
                                />
                            </div>
                        </div>
                    </div>
                    <div
                        className="card-theme-inverse centered-p"
                        style={{
                            height: 300,
                            display: "flex",
                            flexDirection: "column",
                            justifyContent: "center",
                        }}
                    >
                        <div style={{ fontFamily: "Avante Garde Bold" }}>
                            or watch live on youtube or twitch
                        </div>
                        <div className="button-wrapper">
                            <div>
                                <YoutubeIcon fill={BLUE} />
                            </div>
                            <div>
                                <TwitchIcon fill={BLUE} />
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
}
